import cartlist from '@/components/cartlist/cartlist.vue'
import {
	mapState,
	mapMutations,
	mapGetters
} from 'vuex'
import {
	myRequestPost
} from '@/utils/request.js';
export default {
	components: {
		cartlist
	},
	data() {
		return {
			buttonList: [{
				title: '删除',
				background: '#ff3b32'
			}]
		}
	},
	methods: {
		changeMethod(data, button, index) {
			console.log('滑动按钮回调', data)
			console.log('滑动按钮回调', button)
			let self = this
			console.log('点击删除')
			if (button.title === '删除') {
				uni.showModal({
					title: '',
					content: '确定要删除该信息吗？',
					confirmText: '删除',
					confirmColor: '#ff3b32',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定')
							self.carts.splice(index, 1)
							uni.showToast({
								icon: "success",
								title: '删除成功!',
								duration: 2000
							});
						} else if (res.cancel) {
							console.log('用户点击取消')
						}
					}
				})
			}
		},
		clickMethod(data) {
			console.log('点击行回调', data)
		}
	},
	computed: {
		...mapState({
			carts: "carts"
		})
	}
